// Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ðµ Ð²Ñ‹ÑÐ¾Ñ‚Ñ‹ Ñƒ header Ð² Ð¿Ñ€Ð¾Ñ†ÐµÑÑÐµ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸ Ð¾ÐºÐ½Ð°
const headerHeight = document.getElementById('headerId');
const headerButton = document.getElementById('buttonId');
const headerAvatar = document.getElementById('avatarId');
headerHeight.classList.remove('_header-min');
headerButton.classList.remove('_button-min');
headerAvatar.classList.remove('_avatar-min');
const minY = 200;
window.onscroll = function () {
  // Ð¾Ñ‚ÑÐ»ÐµÐ¶Ð¸Ð²Ð°ÐµÐ¼ ÐºÐ¾Ð¾Ñ€Ð´Ð¸Ð½Ð°Ñ‚Ñ‹ Ð¿Ð¾ Ð¾ÑÐ¸ Y
  const pageY = () => (window.pageYOffset || window.scrollY);
  let scrollYPos = pageY();
  // setTimeout(() => {

    // if ÐºÐ¾Ð¾Ñ€Ð´Ð¸Ð½Ð°Ñ‚Ñ‹ Ð±Ð¾Ð»ÑŒÑˆÐµ minY, Ñ‚Ð¾ ÑƒÐ¼ÐµÐ½ÑŒÑˆÐ°ÐµÐ¼ Ð²Ñ‹ÑÐ¾Ñ‚Ñƒ header, else Ð¾ÑÑ‚Ð°Ð²Ð»ÑÐµÐ¼ Ð¿Ñ€ÐµÐ¶Ð½ÐµÐ¹
    if (scrollYPos >= minY) {
      headerHeight.classList.add('_header-min');
      headerButton.classList.add('_button-min');
      headerAvatar.classList.add('_avatar-min');
    } else {
      headerHeight.classList.remove('_header-min');
      headerButton.classList.remove('_button-min');
      headerAvatar.classList.remove('_avatar-min');
    };
  // }, 200); // Ð²Ñ€ÐµÐ¼Ñ transition Ð² CSS
};

// ÑƒÐ±Ñ€Ð°Ñ‚ÑŒ ÑÐºÑ€Ð¾Ð»Ð» ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹ Ð¿Ð¾ÑÐ»Ðµ Ð¾Ñ‚Ð¾Ð±Ñ€Ð°Ð¶ÐµÐ½Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
document.addEventListener("DOMContentLoaded", function () {
  // Ð²Ñ‹Ñ‡Ð¸ÑÐ»ÑÐµÐ¼ ÑˆÐ¸Ñ€Ð¸Ð½Ñƒ Ð¿Ð¾Ð»Ð¾ÑÑ‹ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸ Ð¸ Ð±ÐµÑ€ÐµÐ¼ ÐµÐµ Ð¼Ð¾Ð´ÑƒÐ»ÑŒ
  const scrollbar = Math.abs(document.body.clientWidth - window.innerWidth) + 'px';
  console.log(scrollbar);

  const pageOffset = () => (window.pageYOffset || window.scrollY);
  let prevBodyOverflow = document.body.style.overflow || 'initial'; // ÑÐ¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ overflow Ð½Ð° ÑÑ‚Ð°Ñ€Ñ‚Ðµ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹
  let prevScrollYPosition = 0; // Ð²Ð²Ð¾Ð´Ð¸Ð¼ Ð¿ÐµÑ€ÐµÐ¼ÐµÐ½Ð½ÑƒÑŽ Ð´Ð»Ñ ÑÐ¾Ñ…Ñ€Ð°Ð½ÐµÐ½Ð¸Ñ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€Ð° scrollY

  // Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð¾Ñ‚Ñ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°ÑŽÑ‰Ð°Ñ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  function openModal(selector) {
    prevScrollYPosition = pageOffset(); // ÑÐ¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€Ð° scrollY
    prevBodyOverflow = document.body.style.overflow; // ÑÐ¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ overflow Ð´Ð¾ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ Ð¼Ð¾Ð´Ð°Ð»ÐºÐ¸
    const el = document.getElementById(selector);
    el.classList.add('_opened'); // Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð¼Ð¾Ð´Ð¸Ñ„Ð¸ÐºÐ°Ñ‚Ð¾Ñ€ _opened
    document.body.style.overflow = 'hidden'; // ÑÐºÑ€Ñ‹Ð²Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ð¾ÑÑƒ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸
    document.body.style.marginRight = scrollbar; // ÐºÐ¾Ð¼Ð¿ÐµÐ½ÑÐ¸Ñ€ÑƒÐµÐ¼ Ð¾Ñ‚ÑÑƒÑ‚ÑÐ²Ð¸Ðµ Ð¿Ð¾Ð»Ð¾ÑÑ‹ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸ (Ð¸Ð½Ð°Ñ‡Ðµ Ð±ÑƒÐ´ÐµÑ‚ ÑÐºÐ°Ñ‡ÐºÐ¾Ð¾Ð±Ñ€Ð°Ð·Ð½Ð½Ð¾Ðµ ÑÐ¼ÐµÑ‰ÐµÐ½Ð¸Ðµ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹)
    btnOpenElem.classList.remove('_visible');
  }
  // Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð¾Ñ‚Ñ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°ÑŽÑ‰Ð°Ñ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
  function closeModal(selector) {
    const el = document.getElementById(selector);
    el.classList.remove('_opened'); // ÑƒÐ´Ð°Ð»ÑÐµÐ¼ Ð¼Ð¾Ð´Ð¸Ñ„Ð¸ÐºÐ°Ñ‚Ð¾Ñ€ _opened
    // Ð¶Ð´ÐµÐ¼ Ð¿Ð¾ÐºÐ° Ð¾Ñ‚Ñ€Ð°Ð±Ð¾Ñ‚Ð°ÐµÑ‚ transition Ð² CSS, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð²ÐµÑ€Ð½ÑƒÑ‚ÑŒ Ð¿Ð¾Ð»Ð¾ÑÑƒ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ¸
    setTimeout(() => {
      document.body.style.overflow = prevBodyOverflow;
      document.body.style.marginRight = 0;
      btnOpenElem.classList.add('_visible');
    }, 200); // Ð²Ñ€ÐµÐ¼Ñ transition Ð² CSS
  }

  // ÑÐ¼Ð¾Ñ‚Ñ€Ð¸Ð¼ Ð½Ð° ÐºÐ°ÐºÑƒÑŽ ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð½Ð°Ð¶Ð°Ð»Ð¸
  // ÑÑ‚Ð¾ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð²Ñ‹Ð·Ñ‹Ð²Ð°ÑŽÑ‰Ð¸Ðµ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÐºÐ¸
  const modalTrigger = Array.from(document.querySelectorAll('[mobile-menu]')); // Ñ„Ð¾Ñ€Ð¼Ð¸Ñ€ÑƒÐµÐ¼ Ð¼Ð°ÑÑÐ¸Ð² Ð¸Ð· Ð²ÑÐµÑ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‰Ð¸Ñ… mobile-menu
  console.log('modalTrigger = ' + modalTrigger); // Ð¿Ñ€Ð¾Ð²ÐµÑ€ÑÐµÐ¼, Ñ‡Ñ‚Ð¾ Ð¾Ð½ ÑÑ„Ð¾Ñ€Ð¼Ð¸Ñ€Ð¾Ð²Ð°Ð»ÑÑ
  // Ð¿ÐµÑ€ÐµÐ±Ð¸Ñ€Ð°ÐµÐ¼ Ð¼Ð°ÑÑÐ¸Ð² Ð¸ Ð²Ñ‹Ð´ÐµÐ»ÑÐµÐ¼ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¿Ð¾ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼Ñƒ ÐºÐ»Ð¸ÐºÐ½ÑƒÐ»Ð¸
  modalTrigger.forEach(element => {
    element.addEventListener('click', event => {
      const targetModalId = event.target.attributes['mobile-menu'].value;
      console.log('targetModalId = ' + targetModalId); // Ð¿Ñ€Ð¾Ð²ÐµÑ€ÑÐµÐ¼ Ñ‚Ð¾Ñ‚ Ð»Ð¸ ÑÑ‚Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚
      openModal(targetModalId); // Ð¾Ð±Ñ€Ð°Ñ‰Ð°ÐµÐ¼ÑÑ Ðº Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸, ÐºÐ¾Ñ‚Ð¾Ñ€Ð°Ñ Ð¾Ñ‚ÐºÑ€Ð¾ÐµÑ‚ Ð¼Ð¾Ð´Ð°Ð»ÐºÑƒ
    });
  });

  // ÑÐ¼Ð¾Ñ‚Ñ€Ð¸Ð¼ Ð½Ð° ÐºÐ°ÐºÑƒÑŽ ÐºÐ½Ð¾Ð¿ÐºÑƒ Ð½Ð°Ð¶Ð°Ð»Ð¸
  // ÑÑ‚Ð¾ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð²Ñ‹Ð·Ñ‹Ð²Ð°ÑŽÑ‰Ð¸Ðµ Ð·Ð°ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ð¼Ð¾Ð´Ð°Ð»ÐºÐ¸
  const modalCloseTrigger = Array.from(document.querySelectorAll('[mobile-menu-close]'));
  console.log(modalCloseTrigger);
  modalCloseTrigger.forEach(element => {
    element.addEventListener('click', event => {
      const targetModalId = event.target.attributes['mobile-menu-close'].value;
      console.log('targetModalId = ' + targetModalId);
      closeModal(targetModalId);
    });
  });


  // ------------- ÐžÑ‚Ð¾Ð±Ñ€Ð°Ð¶Ð°Ñ‚ÑŒ ÑÐºÑ€Ñ‹Ñ‚Ñ‹Ð¹ Ñ‚ÐµÐºÑÑ‚ Ð² ÑÐµÐºÑ†Ð¸Ð¸ about  -----------------

  // Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð¾Ñ‚Ñ€Ð°Ð±Ð°Ñ‚Ñ‹Ð²Ð°ÑŽÑ‰Ð°Ñ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ Ñ‚ÐµÐºÑÑ‚Ð° Ð¸ ÑÐ¾ÐºÑ€Ñ‹Ñ‚Ð¸Ðµ ÐºÐ½Ð¾Ð¿ÐºÐ¸
  function openText(textSelector, buttonSelector) {

    const moreText = document.getElementById(textSelector);
    const btnText = document.getElementById(buttonSelector);

    console.log('btn "See more..." was pressed');

    btnText.classList.add('_hide-button');
    moreText.classList.remove('_hide-text');
    moreText.classList.add('_visible-text');
  }
  // Ð½Ð°Ð¶Ð°Ð»Ð¸ Ð½Ð° 1-ÑŽ ÐºÐ½Ð¾Ð¿ÐºÑƒ
  document.getElementById("btn-1").addEventListener('click', event => {
    openText('more-1', 'btn-1');
  });
  // Ð½Ð°Ð¶Ð°Ð»Ð¸ Ð½Ð° 2-ÑŽ ÐºÐ½Ð¾Ð¿ÐºÑƒ
  document.getElementById("btn-2").addEventListener('click', event => {
    openText('more-2', 'btn-2');
  });
  // Ð½Ð°Ð¶Ð°Ð»Ð¸ Ð½Ð° 2-ÑŽ ÐºÐ½Ð¾Ð¿ÐºÑƒ
  document.getElementById("btn-3").addEventListener('click', event => {
    openText('more-3', 'btn-3');
  });
});